/* base styles  */

// Base selectors 
html {
    box-sizing: border-box;
    font-size: 100%;
    scroll-behavior: smooth;
}

body {
    height: auto;
    width: auto;
    color: white;
    font-family: raleway-font-stack;
}

h1 {
    @include h1-vr;
}

h2 {
    @include h2-vr;
    line-height: 2.25rem;;
}

h3 {
    @include h3-vr;
}

h4 {
    @include h4-vr;
}

p {
    @include p-vr;
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        font-weight: 600;
    }
}

li {
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        font-weight: 600;
    }
}

img {
    max-width: 100%;
    max-height: 100%;
}



// Margin wrappers
.horizontal-margins {
    margin-left: 15px;
    margin-right: 15px;
}

.vertical-margins {
    margin-top: 20px;
    margin-bottom: 20px;
}

.vertical-margins-top-only {
    margin-top: 20px;
}
.vertical-margins-bottom-only {
    margin-bottom: 20px;
}


// width containers 

// The following container will be the maximum width of any element of the site, at any viewport
.horizontal-container {
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width:map-get($breakpoints, "tablet")) {
        max-width: map-get($breakpoints, "tablet") - $nav-bar-width !important;
    }
    @media screen and (min-width:map-get($breakpoints, "desktop")) {
        max-width: map-get($breakpoints, "desktop") - $nav-bar-width !important;
    }
    @media screen and (min-width:map-get($breakpoints, "desktop-wide")) {
        max-width: map-get($breakpoints, "desktop-wide") - $nav-bar-width !important;
    }

    @media screen and (min-width:map-get($breakpoints, "beyond-wide")) {
        max-width: map-get($breakpoints, "beyond-wide") - $nav-bar-width !important;
    }
}




